// src/pages/ChatbotFileUpload/ChatbotFileUpload.jsx

import React, { useState, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import './ChatbotFileUpload.css';
import { pythonApi } from '../../services/api';

const ChatbotFileUpload = () => {
  const navigate = useNavigate();
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [isDragging, setIsDragging] = useState(false);

  const addFiles = useCallback((newFiles) => {
    const filesArray = Array.from(newFiles);
    setSelectedFiles((prevFiles) => {
      const uniqueFiles = filesArray.filter(
        (newFile) =>
          !prevFiles.some(
            (prevFile) =>
              prevFile.name === newFile.name && prevFile.size === newFile.size
          )
      );
      return [...prevFiles, ...uniqueFiles];
    });
  }, []);

  const handleFileChange = (e) => {
    addFiles(e.target.files);
  };

  const handleDragOver = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback(
    (e) => {
      e.preventDefault();
      e.stopPropagation();
      setIsDragging(false);
      if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
        addFiles(e.dataTransfer.files);
        e.dataTransfer.clearData();
      }
    },
    [addFiles]
  );

  const handleRemoveFile = (fileName) => {
    setSelectedFiles((prevFiles) =>
      prevFiles.filter((file) => file.name !== fileName)
    );
  };

  const handleUploadFiles = async () => {
    if (selectedFiles.length === 0) {
      alert('    .');
      return;
    }

    const formData = new FormData();
    selectedFiles.forEach((file) => {
      formData.append('file', file);
    });

    try {
      const response = await pythonApi.post('/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      alert('      !');
      setSelectedFiles([]);
      navigate('/prompt/chatbot');
    } catch (error) {
      if (error.response?.status === 401) {
        alert('   .  .');
        navigate('/login');
      } else {
        alert('  .  .');
      }
    }
  };

  const goHome = () => navigate('/');
  const goToMyPage = () => navigate('/mypage');

  return (
    <div className="chatbot-upload-container">
      {/*    */}
      <header className="header">
        <img
          src="/fopofo-logo.png"
          alt=" "
          className="logo"
          onClick={goHome}
        />
        <button className="mypage-button" onClick={goToMyPage}>
          my page
        </button>
      </header>

      {/*    */}
      <div className="main-center-wrapper">
        <h2 className="title">Upload files for Chatbot</h2>

        <div
          className={`content-box ${isDragging ? 'dragging' : ''}`}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
        >
          <p className="instruction">   .</p>
          <p className="subtext">
            (      <br />
                  .)
          </p>

          <label htmlFor="file-upload" className="file-upload-label">
             
          </label>
          <input
            id="file-upload"
            type="file"
            className="file-input"
            onChange={handleFileChange}
            multiple
            hidden
          />
          <span className="selected-files-text">
            {selectedFiles.length > 0
              ? `${selectedFiles.length}  `
              : '  '}
          </span>

          {selectedFiles.length > 0 && (
            <ul className="file-list">
              {selectedFiles.map((file, index) => (
                <li key={index} className="file-item">
                  {file.name}
                  <button
                    type="button"
                    onClick={() => handleRemoveFile(file.name)}
                    className="remove-file-button"
                  >
                    X
                  </button>
                </li>
              ))}
            </ul>
          )}

          <div className="footer">
            <button className="next-button" onClick={handleUploadFiles}>
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatbotFileUpload;
