import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { nodeApi } from "../services/api"; 
import { useAuth } from '../contexts/AuthContext';

export default function CreateUser() {
    //  name -> nickname    ( )
    const [nickname, setNickname] = useState("");
    const navigate = useNavigate();
    const { updateUserNickname } = useAuth();

    //    API    
    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!nickname) return;

        try {
            //    API 
            await nodeApi.put('/users/set-nickname', { nickname });

            updateUserNickname(nickname);
            
            alert(' .    .');
            navigate(`/intro-upload`); //     

        } catch (error) {
            //       
            alert(error.response?.data?.message || ' .');
        }
    };

    return (
        <form onSubmit={handleSubmit} className="flex flex-col items-center gap-4 mt-40">
            <input
                type="text"
                placeholder="  " //   
                className="px-4 py-2 rounded shadow"
                value={nickname} //  state 
                onChange={(e) => setNickname(e.target.value)} //  state 
            />
            <button type="submit" className="bg-blue-500 text-white px-6 py-2 rounded hover:bg-blue-600">
                 
            </button>
        </form>
    );
}