import axios from 'axios';

//  1. Node.js   axios  ( ,   )
export const nodeApi = axios.create({
  baseURL: 'http://localhost:5000/api', // Node.js   URL
});

//  2. Python(FastAPI)   axios  ( AI  )
export const pythonApi = axios.create({
  baseURL: 'http://localhost:8000', // Python   URL
});

//  3.        
const addAuthToken = (config) => {
  const token = localStorage.getItem('token'); //    
  if (token) {
    config.headers.Authorization = `Bearer ${token}`; //  'Bearer ' 
  }
  return config;
};

//  4.      
nodeApi.interceptors.request.use(addAuthToken, (error) => Promise.reject(error));
pythonApi.interceptors.request.use(addAuthToken, (error) => Promise.reject(error));

