// src/contexts/AuthContext.jsx

//  1.  useEffect  .
import React, { createContext, useState, useContext, useEffect } from 'react';
import { nodeApi } from '../services/api';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
    const [user, setUser] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    //  2.        .
    useEffect(() => {
        const token = localStorage.getItem('token');
        if (token) {
            //  "  " API .
            nodeApi.get('/users/me')
                .then(response => {
                    //  ,     .
                    setUser(response.data);
                })
                .catch(() => {
                    //     
                    localStorage.removeItem('token');
                    setUser(null);
                })
                .finally(() => setIsLoading(false));
        } else {
            setIsLoading(false);
        }
    }, []); //           .

    //    
    const login = (userData) => {
        const { token, user } = userData;
        localStorage.setItem('token', token);
        setUser(user);
    };
    
    //     
    const updateUserNickname = (nickname) => {
        setUser(prevUser => ({ ...prevUser, nickname }));
    };

    //    
    const logout = async () => {
        try {
            //    .
            await nodeApi.post('/users/logout');
        } catch (error) {
            //         .
            console.error("  :", error);
        } finally {
            //    .
            localStorage.removeItem('token');
            //  user  null    .
            setUser(null);
        }
    };

    //         ( )
    if (isLoading) {
        return <div>Loading...</div>; 
    }

    return (
        <AuthContext.Provider value={{ user, login, updateUserNickname, logout, isAuthenticated: !!user }}>
            {children}
        </AuthContext.Provider>
    );
};

//    user       custom hook
export const useAuth = () => {
    return useContext(AuthContext);
};