import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import QRCode from 'react-qr-code';
import './PortfolioCreatedPage.css';

function PortfolioCreatedPage() {
    const [copied, setCopied] = useState(false);
    const [progress, setProgress] = useState(0);
    const [showQR, setShowQR] = useState(false);
    const [isCreated, setIsCreated] = useState(false); //  useState    

     //       URL  
     const [portfolioUrl, setPortfolioUrl] = useState('');
     const location = useLocation();
     const navigate = useNavigate();
 
     //    ,    URL state .
     useEffect(() => {
         if (location.state && location.state.portfolioUrl) {
             setPortfolioUrl(location.state.portfolioUrl);
         } else {
             // URL      
             console.error(" URL  .");
             alert(" .   .");
             navigate('/mainpage');
         }
     }, [location.state, navigate]);

    const handleCopy = async () => {
        try {
            await navigator.clipboard.writeText(portfolioUrl);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('  ');
        }
    };

    useEffect(() => {
        const interval = setInterval(() => {
            setProgress(prev => {
                if (prev >= 100) {
                    clearInterval(interval);
                    setShowQR(true);
                    setIsCreated(true); //    !
                    return 100;
                }
                return prev + 1;
            });
        }, 50);
        return () => clearInterval(interval);
    }, []);


    return (
        <div className="url-page-wrapper">
            <img
                src="/fopofo-logo.png"
                alt="fopofo-logo"
                className="logo"
                onClick={() => window.location.href = '/mainpage'}
            />
            <button
                className="mypage-button-created"
                onClick={() => navigate('/mypage')}
            >
                my page
            </button>

            {/*          */}
            <div
                className={`final-banner ${isCreated ? 'hoverable' : ''}`}
                onClick={() => {
                    if (isCreated) {
                        window.open(portfolioUrl, '_blank');
                    }
                }}
            >
                <div className="url-title-box">
                    <h1>PORTFOLIO<br />IS CREATED</h1>
                </div>
            </div>

            <div className="url-box-custom">
                <div className="url-label">URL</div>
                <div className="url-display">{portfolioUrl}</div>
                <button className="copy-button-custom" onClick={handleCopy}></button>
            </div>

            {copied && <span className="copy-feedback"> !</span>}

            {!showQR ? (
                <div className="progress-container">
                    <div className="progress-bar" style={{ width: `${progress}%` }} />
                </div>
            ) : (
                <div className="qr-wrapper">
                    <QRCode value={portfolioUrl} size={100} />
                    <p className="qr-label">QR  </p>
                </div>
            )}
        </div>
    );
}

export default PortfolioCreatedPage;